import React, { Component } from 'react';
import { Parking } from "../lib/Parking";
import { Button } from "react-bootstrap";

class Carpark extends Component {

    getCarparkFeeEstimate = (carparkData, period, offset) => {
        const startParking = new Date(Date.now() + offset * 60 * 1000);
        const parkingPeriod = period * 60;
        const day = startParking.getDay(); // NOTE: Sunday=0, Monday=1, ...
        const hour = startParking.getHours();
        const beginIndex = day * 24 + hour;
        const endIndex = beginIndex + Math.ceil(period) + 2;
        const carparkRatesRaw = [...carparkData.carpark.carpark_rates, ...carparkData.carpark.carpark_rates];
        console.log("carpark_rates length",carparkData.carpark.carpark_rates.length);
        console.log("carparkRatesRaw length",carparkRatesRaw.length);
        const carparkRates = carparkRatesRaw.slice(beginIndex, endIndex);
        const parking = new Parking(carparkRates, startParking, parkingPeriod);
        return { parkingFee: parking.parkingFee, parkingForbidden: parking.parkingForbidden };
    };

    onClickDestination = () => {
        const origLat = (this.props.position !== null) ? this.props.position.coords.latitude : 1.2857166;
        const origLng = (this.props.position !== null) ? this.props.position.coords.longitude : 103.8235629;
        const destLat = this.props.carparkData.carpark.lat;
        const destLng = this.props.carparkData.carpark.lng;
        const url = "https://www.google.com/maps/dir/?api=1&origin=" + origLat + "," + origLng + "&destination=" + destLat + "," + destLng + "&travelmode=driving";
        window.open(url, '_blank');
    }

    renderParkingFee = (estimate) => {
        if (estimate.parkingForbidden === true) {
            return <div className="badge bg-danger">CLOSED</div>;
        } else {
            if (estimate.parkingFee === 0) {
                return <div className="badge bg-primary">FREE</div>;
            } else {
                return <div className="badge bg-primary">S${estimate.parkingFee.toFixed(2)}</div>;
            }
        }
    }

    render() {
        if (this.props.carparkData === null) return;
        const estimate = this.getCarparkFeeEstimate(this.props.carparkData, this.props.period, this.props.offset);
        return (
            <Button
                onClick={this.onClickDestination}
                variant="light"
                className="list-group-item list-group-item-action align-items-start d-flex p-0 py-2"
                style={{ borderRadius: 0 }}>

                <div className="col-9">
                    <div className="fw-bold fs-6">{this.props.carparkData.carpark.name}</div>
                    <div className="fs-6">{this.props.carparkData.carpark.address}</div>
                </div>
                <div className="col-3 text-end">
                    {this.renderParkingFee(estimate)}
                    <div className="badge bg-secondary">{this.props.carparkData.walkingminutes} min walk</div>
                </div>

            </Button>
        );
    }
}

export default Carpark;

