import React, { Component } from 'react';
import Carpark from './Carpark';

class CarparkSection extends Component {
  render() {
    if (this.props.shortlists.length < 1) return;
    return (
      <div className="container pb-2">
        <div className="list-group list-group-flush fs-5">
          <Carpark position={this.props.position} offset={this.props.offset} period={this.props.period} carparkData={(this.props.shortlists.length > 0) ? this.props.shortlists[0] : null} />
          <Carpark position={this.props.position} offset={this.props.offset} period={this.props.period} carparkData={(this.props.shortlists.length > 1) ? this.props.shortlists[1] : null} />
          <Carpark position={this.props.position} offset={this.props.offset} period={this.props.period} carparkData={(this.props.shortlists.length > 2) ? this.props.shortlists[2] : null} />
          <Carpark position={this.props.position} offset={this.props.offset} period={this.props.period} carparkData={(this.props.shortlists.length > 3) ? this.props.shortlists[3] : null} />
          <Carpark position={this.props.position} offset={this.props.offset} period={this.props.period} carparkData={(this.props.shortlists.length > 4) ? this.props.shortlists[4] : null} />
        </div>
      </div>
    );
  }
}

export default CarparkSection;