import "./App.css";
import PlacesAutocomplete from "./components/PlacesAutocomplete";
import CarparkSection from "./components/CarparkSection";
import DistanceMatrixWrapper from "./components/DistanceMatrixWrapper";
import React from "react";
import Carparks from "./data.json";
import { Button } from "react-bootstrap";

class App extends React.Component {
  state = {
    position: null,
    distance: null,
    address: "",
    lat: 1.2851377,
    lng: 103.8306871,
    period: 0.5,
    offset: 0,
    offsetOverride: false,
    calculateDistance: false,
    buttons: [
      'btn-warning',
      'btn-secondary',
      'btn-secondary',
      'btn-secondary',
      'btn-secondary'
    ],
    shortlists: []
  };

  componentDidMount = () => {
    navigator.geolocation.getCurrentPosition((position) => {
      this.setState({ position: position });
    });
  }

  getDistanceFromLatLngInKm = (lat1, lng1, lat2, lng2) => {
    const deg2rad = (deg) => {
      return deg * (Math.PI / 180);
    }
    const R = 6371; // Radius of the earth in km
    const dLat = deg2rad(lat2 - lat1);
    const dLng = deg2rad(lng2 - lng1);
    const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
      Math.sin(dLng / 2) * Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const d = R * c; // Distance in km
    return d;
  }

  getShortlistedCarparks = (lat, lng, maxDistance) => {
    const shortlists = [];
    const walkingSpeed = 5; // 5 km per hour
    const sorter = (a, b) => {
      return a.distance - b.distance;
    }
    const sortByDistance = arr => {
      arr.sort(sorter);
    }
    Carparks.map((carpark) => {
      const distance = this.getDistanceFromLatLngInKm(lat, lng, carpark.lat, carpark.lng);
      const walkingminutes = Math.ceil(distance / walkingSpeed * 60);
      if (distance < maxDistance) {
        shortlists.push({ carpark, distance, walkingminutes });
      }
      return true;
    });
    sortByDistance(shortlists);
    return shortlists;
  }

  onSelectDestination = (destinationData) => {
    const maxDistance = 1;
    const { lat, lng } = destinationData;
    const shortlists = this.getShortlistedCarparks(lat, lng, maxDistance);
    this.setState(destinationData);
    this.setState({ shortlists: shortlists, offsetOverride: false, calculateDistance: true });
  }

  onClickPeriod = (index, period) => {
    let buttons = this.state.buttons;
    for (let i = 0; i < buttons.length; ++i) {
      if (i === index) {
        buttons[i] = 'btn-warning';
      } else {
        buttons[i] = 'btn-secondary';
      }
    }
    this.setState({ period: period });
    this.setState({ buttons: buttons });
  }

  onClickPeriod1 = () => {
    this.onClickPeriod(0, 0.5);
  }

  onClickPeriod2 = () => {
    this.onClickPeriod(1, 1);
  }

  onClickPeriod3 = () => {
    this.onClickPeriod(2, 2);
  }

  onClickPeriod4 = () => {
    this.onClickPeriod(3, 3);
  }

  onClickPeriod5 = () => {
    this.onClickPeriod(4, 6);
  }

  onChangeOffset = (event) => {
    this.setState({ offset: event.target.value, offsetOverride: true });
  }

  onChangeDMDistance = (distance) => {
    this.setState({ distance: distance });
  }

  onChangeDMOffset = (offset) => {
    this.setState({ offset: offset, calculateDistance: false });
  }

  render() {
    return (
      <main>
        <div className="container-fluid bg-secondary pt-5 pb-3">
          <div className="container">
            <div className="text-start text-md-center text-light pt-3 pb-3">
              <h1><span className="fw-bold fs-1">Nearest</span> parking</h1>
            </div>
            <PlacesAutocomplete
              inputs={this.state}
              onSelectDestination={this.onSelectDestination} />
          </div>
        </div>
        <div className="container-fluid">
          <div className="container pt-3 pb-2">
            <div className="input-group pt-1 pb-2">
              <Button className={"btn fs-5 " + this.state.buttons[0]} onClick={this.onClickPeriod1}>30min</Button>
              <Button className={"btn fs-5 " + this.state.buttons[1]} onClick={this.onClickPeriod2}>1hr</Button>
              <Button className={"btn fs-5 " + this.state.buttons[2]} onClick={this.onClickPeriod3}>2hr</Button>
              <Button className={"btn fs-5 " + this.state.buttons[3]} onClick={this.onClickPeriod4}>3hr</Button>
              <Button className={"btn fs-5 " + this.state.buttons[4]} onClick={this.onClickPeriod5}>6hr</Button>
            </div>
            <div className="col-xs-2">
              <select className="form-select fs-5 bg-warning" style={{ width: 200 }} value={this.state.offset} onChange={this.onChangeOffset}>
                <option value="0">arrive now</option>
                <option value="5">arrive in +5min</option>
                <option value="10">arrive in +10min</option>
                <option value="15">arrive in +15min</option>
                <option value="20">arrive in +20min</option>
                <option value="25">arrive in +25min</option>
                <option value="30">arrive in +30min</option>
                <option value="35">arrive in +35min</option>
                <option value="40">arrive in +40min</option>
                <option value="45">arrive in +45min</option>
              </select>
            </div>
          </div>
          <DistanceMatrixWrapper
            inputs={this.state}
            onChangeDMDistance={this.onChangeDMDistance}
            onChangeDMOffset={this.onChangeDMOffset} />
          <CarparkSection
            position={this.state.position}
            offset={this.state.offset}
            period={this.state.period}
            shortlists={this.state.shortlists} />
        </div>
      </main>
    );
  }
}

export default App;

